<template>
  <router-link :to="{ name: 'product', params: { id: item.id } }">
    <div class="item-container">
      <img :src="item.thumbnail" :alt="item.title" class="thumbnail" />
      <div class="item-info">
        <div class="title-price">
          <h1 class="title">{{ item.title }}</h1>
          <div class="price">{{ item.price }} $</div>
        </div>
        <p>{{ item.stock }} in stock</p>
      </div>

      <div class="bid-info">
        <div class="highest-bid">
          <img src="@/assets/images/productCard/candlesticks.svg" alt="" />
          <p>Highest bid <strong>{{ highestBid }} $</strong></p>
        </div>
        <p class="inWishlist" v-if="item.wish">In my wishlist ðŸ”¥</p>
      </div>
    </div>
  </router-link>
</template>
<script>
export default {
  name: "ItemCard",
  props: {
    item: Object,
  },
  computed: {
    highestBid() {
      if (this.item.bids.length <= 0) return +this.item.price
      return this.item.bids[this.item.bids.length - 1]
    }
  }
};
</script>
